import React from 'react';

import ApartmentsSubject from './LPGStationsSubject';

import data from '../map-components/lpgstationsdb';

function LPGStationsList() {
  const lpgStationsItems = data.map(({
    key, position, heading, description,
  }) => (
    <ApartmentsSubject
      key={key}
      position={position}
      heading={heading}
      description={description}
    />
  ));
  return (
    <div style={{ display: 'none' }}>{lpgStationsItems}</div>
  );
}

export default LPGStationsList;
